* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.geoContainer {
  display: flex;
  justify-content: center;
  margin-top: 4px;
}

.geoButton {
  background-color: transparent;
  border: solid 0px;
  border-radius: 8px;
  padding: 2px;
  cursor: pointer;
}

.error__loc {
  font-size: x-large;
  color: rgba(255, 0, 51, 0.66);
  font-weight: bold;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
body {
  min-height: 100vh;
  position: relative;
  height: 100%;
  width: 100%;
  background-image: url("./assets/bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

body::-webkit-scrollbar {
  display: none;
}
