@import url(https://fonts.googleapis.com/css?family=Raleway:400,700,900);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.geoContainer {
  display: flex;
  justify-content: center;
  margin-top: 4px;
}

.geoButton {
  background-color: transparent;
  border: solid 0px;
  border-radius: 8px;
  padding: 2px;
  cursor: pointer;
}

.error__loc {
  font-size: x-large;
  color: rgba(255, 0, 51, 0.66);
  font-weight: bold;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
body {
  min-height: 100vh;
  position: relative;
  height: 100%;
  width: 100%;
  background-image: url(/weather/static/media/bg.f03b5cf7.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

body::-webkit-scrollbar {
  display: none;
}

.search__form {
  margin: 0 auto;
  background-color: transparent;
  font-family: "Raleway", sans-serif;
  display: flex;
  justify-content: center;
  width: 70%;
  margin-left: 0px;
  margin-right: 0px;
}

.search__container {
  padding-top: 64px;
  display: flex;
  justify-content: center;
  grid-column-gap: 4px;
  -webkit-column-gap: 4px;
          column-gap: 4px;
}

.search__input {
  width: 100%;
  padding: 12px 24px;
  background-color: transparent;
  transition: -webkit-transform 250ms ease-in-out;
  transition: transform 250ms ease-in-out;
  transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
  background-color: transparent;
  background-repeat: no-repeat;
  background-size: 18px 18px;
  background-position: 95% center;
  border-radius: 50px;
  border: 1px solid #ffffff;
  transition: all 250ms ease-in-out;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  margin-right: 4px;
}

.search__input::-webkit-input-placeholder {
  color: #1f1d1d;
  letter-spacing: 1.5px;
}

.search__input::placeholder {
  color: #1f1d1d;
  letter-spacing: 1.5px;
}

.search__input:hover,
.search__input:focus {
  padding: 12px 0;
  outline: 0;
  border: 2px solid transparent;
  border-bottom: 2px solid #0003c5;
  border-radius: 0;
  background-position: 100% center;
}

.search__button {
  border: solid 1px white;
  border-radius: 32px;
  padding-left: 5px;
  padding-right: 5px;
  background-color: transparent;
  cursor: pointer;
}

.search__favoriteContainer {
  margin-top: 52px;
  display: flex;
  position: absolute;
  border: solid 1px white;
  background-color: whitesmoke;
  padding: 8px;
  border-radius: 8px;
  flex-direction: column;
}

.search__favorites {
  display: flex;
}

.search__favoriteItem {
  cursor: pointer;
}

.lds-ripple {
    display: inline-block;
    width: 80px;
    height: 80px;
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.lds-ripple div {
position: absolute;
border: 4px solid #fff;
opacity: 1;
border-radius: 50%;
-webkit-animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
        animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
-webkit-animation-delay: -0.5s;
        animation-delay: -0.5s;
}
@-webkit-keyframes lds-ripple {
0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
}
100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
}
}
@keyframes lds-ripple {
0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
}
100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
}
}
